<template>
    <v-lazy-image
        v-if="src && !failedToLoad"
        class="icon icon_filled"
        :src="src"
        :class="{
            icon_vertical: vertical,
            icon_loading: !isLoaded
        }"
        :intersection-options="{rootMargin: '500px'}"
        @error="failedToLoad = true"
        @load="isLoaded = true"
    />
    <div
        v-else
        class="icon"
        :class="{ icon_vertical: vertical }"
    ></div>
</template>

<script>
import VLazyImage from 'v-lazy-image/v2';

export default {
  props: {
    src: String,
    vertical: Boolean,
  },
  components: {
    VLazyImage,
  },
  data() {
    return {
      isLoaded: false,
      failedToLoad: false,
    };
  },
};
</script>

<style lang="scss" scoped>
.icon {
    object-fit: cover;
    object-position: center;
    width: 80px;
    height: 80px;

    &_loading {
        opacity: 0;
    }

    &:not(&_vertical) {
        border-radius: 22.5%;
        mask: url(../../assets/ios-mask.svg) center/100% 100% no-repeat;
    }

    &_vertical {
        border-radius: 10px;
        height: 125px;
    }

    &:not(&_filled) {
        background-color: var(--stub-bg);
    }
}
</style>
