        <span v-if="isInStore" class="app__public">
          {{ app.is_public ? $t('PUBLIC') : $t('PRIVATE') }}
        </span>
        <span v-if="app.price" class="app__price"
          ><img class="app__price-icon" src="@/assets/icon-price.svg" />
