</template>

<script>
import prettysize from 'prettysize';
import installAppMixin from '@/mixins/installApp';
import VLazyImage from 'v-lazy-image/v2';
import { operatingSystems } from '@/data';

export default {
  mixins: [installAppMixin],
  components: {
    VLazyImage,
  },
  props: {
    isRequestable: Boolean,
    isUpdatable: Boolean,
    isInStore: Boolean,
    isInRepo: Boolean,
    app: Object,
    type: String,
    repoId: String,
    index: Number,
    link: String,
  },
  data() {
    return {
      deletingIpa: false,
      installingIpa: false,
      iconFailedToLoad: false,
      iconLoaded: false,
    };
  },
  computed: {
    computedLink() {
      if (this.link) {
        return this.link;
      }

      if (this.isInStore) {
        return '';
      }

      return `/app/${(this.type || this.app.type)}/${(this.isRequestable || this.isUpdatable) ? this.app.trackid : this.app.id}`;
    },
    genres() {
      return this.$store.getters.genres;
    },
    compatibilityString() {
      const devices = operatingSystems.filter(({ name }) => this.app[`min_${name}_version`]).map(({ device }) => device);

      if (!devices.length) {
        return '';
      }

      return this.$t('FOR_X', [devices.join(', ')]);
    },
    icon() {
      return this.app.image || this.app.iconURL || this.app.icon_uri;
    },
  },
  methods: {
    formatDate(timestamp) {
      return new Date(timestamp).toLocaleString();
    },
    deleteIpa() {
      if (!this.isInStore || this.deletingIpa) {
        return;
      }

      this.$confirm(this.$t('DELETE_APP_WARNING')).then(() => {
        this.deletingIpa = true;
        this.$api.deleteIpa(this.app.id).then(() => {
          this.$emit('update');
        });
      });
    },
    installIpa() {
      if (this.installingIpa) {
        return;
      }

      if (this.app.no_installation_ticket_failure_reason) {
        this.$notify({
          type: 'error',
          text: this.app.no_installation_ticket_failure_reason.translated,
        });
        return;
      }

      this.installingIpa = true;

      this.installApp(this.app.id, 'universal', { installationTicket: this.app.installation_ticket }).finally(() => { this.installingIpa = false; });
    },
    prettysize,
  },
};
</script>

