<template>
  <section>
    <h2 class="heading">
      <router-link v-if="!isRepo" :to="`/search/?type=${type}`" class="heading__link">
        <span class="heading__label">
          <img
            v-if="labelIcon"
            class="heading__label-icon"
            :class="{ 'heading__label-icon_alone': !label }"
            :src="labelIcon"
          />
          {{ label }}
        </span>
        <span class="heading__text">{{ heading || $t("APPS") }}</span>
        <img class="heading__icon"
          :src="require(`@/assets/themes/${BRAND_KEY}/images/icon-chevron.svg`)" />
      </router-link>
      <span v-else class="heading__text">{{ heading }}</span>


    </h2>

    <slot name="new">
      <div
        v-if="isRepo ? !list.new.length : !list.new.length || !list.top.length"
        class="apps"
      >
        <div class="app app_stub" v-for="n in 24" :key="n">
          <div class="app__link">
            <app-icon :vertical="vertical" />
            <span class="app__title">t</span>
            <div class="app__category">t</div>
          </div>
        </div>
      </div>
      <div v-else class="apps" v-show="tab === 'new'">
        <div
          class="app"
          :class="{'app_repo': isRepo}"
          v-for="(app, index) in list.new"
          :key="app.id"
        >
          <router-link
            :to="isRepo ? `/repos/${repoId}/app/${app.idxKey}` : `/app/${type}/${app.id}`"
            class="app__link"
          >
            <app-icon :src="app.image || app.iconURL" :vertical="vertical" />

            <span class="app__title">{{ app.name }}</span>
            <span
              v-if="isRepo"
              class="app__category app__category_wrap"
            >
              {{ app.developerName }}
            </span>
            <router-link
              v-else
              class="app__category"
              :to="`/search/?type=${type}&genre=${app.genre_id}`"
            >
              {{ $t(genres[type][app.genre_id].name.toUpperCase()) }}</router-link
            >
          </router-link>
        </div>
        <div v-if="list.new.length % 8" class="apps__spacer"></div>
      </div>
    </slot>
  </section>
</template>

<script>
import AppIcon from './AppIcon.vue';

export default {
  props: {
    label: String,
    labelIcon: String,
    heading: String,
    list: Object,
    type: String,
    vertical: Boolean,
  },
  components: {
    AppIcon,
  },
  data() {
    return {
      tab: 'new',
      iconsFailedToLoad: {},
      loadedIcons: {},
    };
  },
  computed: {
    genres() {
      return this.$store.getters.genres;
    },
    repoId() {
      return this.$route.params.id;
    },
    isRepo() {
      return this.type === 'repo';
    },
  },
};
</script>

