<template>
  <section class="main">
    <div class="main__content">
      <template v-if="loaded">
        <back-link v-if="belongsToUser" :to="`/my/repos`" class="back-link">
          {{ $t('MY_REPOSITORIES') }}
        </back-link>
        <back-link v-else :to="`/repos`" class="back-link">
          {{ $t('PUBLIC_REPOS') }}
        </back-link>
      </template>

      <h1 class="heading">{{ name }}</h1>
      <div v-if="loaded && apps.length" class="filters">
        <h2 class="filters__heading">
          <img class="filters__heading-icon"
            :src="require(`@/assets/themes/${BRAND_KEY}/images/icon-filter.svg`)" />
          {{ $t("FILTER_BY") }}
        </h2>
        <input :placeholder="$t('NAME')" @input="onFilterInput" class="filters__input" />
      </div>

      <loader v-if="!loaded" />
      <p v-else-if="!apps.length">{{ $t('NO_APPS_IN_REPO') }}</p>
      <template v-else>
        <template v-if="isMobile">
          <app
            v-for="(app, idx) in filteredApps"
            :key="idx"
            :app="app"
            :link="`/repos/${id}/app/${app.idxKey}`"
            is-in-repo
          />
        </template>
        <apps
          v-else-if="filteredApps.length"
          class="main__apps" :list="{new: filteredApps, top: []}" type="repo" />
      </template>
    </div>

    <aside class="main__sidebar">
      <div class="block block_yellow">
        <div class="block__top">
          <div class="block__left">
            <img src="@/assets/icon-news-sidebar.svg" />
          </div>
          <div class="block__right">
            <h2 class="block__heading">{{ $t("REPOSITORY_NEWS") }}</h2>
            <ul class="block__list" v-if="!loaded">
              <li class="block__item block__item_stub" v-for="n in 3" :key="n">
                n
              </li>
            </ul>
            <p v-else-if="!news.length">
              {{ $t('NO_NEWS_IN_REPO') }}
            </p>
            <ul v-else class="news">
              <li v-for="item in news" :key="item.id" class="block__item">
                <span class="news__title">{{ item.title }}</span>
                <span class="news__caption">{{ item.caption }}</span>
                <span class="news__date">{{ new Date(item.date).toLocaleDateString() }}</span>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </aside>
  </section>
</template>

<script>
import Apps from '@/components/Apps/index.vue';
import App from '@/components/App';
import BackLink from '@/components/BackLink';
import Loader from '@/components/Loader';
import { debounce } from 'lodash';
import axios from 'axios';

export default {
  components: {
    Apps,
    App,
    BackLink,
    Loader,
  },
  computed: {
    title() {
      return this.$t('SITE_TITLE');
    },
    id() {
      return this.$route.params.id;
    },
    filteredApps() {
      const filter = this.filter.toLowerCase().trim();
      return this.apps.filter(app => app.name.toLowerCase().includes(filter));
    },
  },
  data() {
    return {
      isMobile: window.innerWidth <= 767,
      name: '',
      apps: [],
      news: [],
      belongsToUser: true,
      loaded: false,
      filter: '',
    };
  },
  methods: {
    getRepo() {
      this.$api.getRepo(this.id)
        .then((response) => {
          if (!response.success) {
            throw response;
          }
          this.belongsToUser = response.data[0].is_yours === 1;
          const repoObject = this;
          axios.get(response.data[0].contents_uri).then((contentsResponse) => {
            const contents = contentsResponse.data;
            repoObject.name = contents.name;
            repoObject.apps = contents.apps.map((app, index) => ({
              key: app.bundleIdentifier + app.size + app.version,
              idxKey: index,
              ...app,
            }));
            console.log(repoObject.apps );
            repoObject.news = [];
            if (contents.news) {
              repoObject.news = contents.news.sort((a, b) => new Date(b.date) - new Date(a.date));
            }
            repoObject.loaded = true;
          });
        })
        .catch((response) => {
          this.$router.replace({
            name: 'error',
            params: {
              message: response.errors[0].translated,
              link: '/my/repos',
              linkText: 'MY_REPOSITORIES',
            },
          });
        })
        .finally(() => {
          // this.loaded = true;
        });
    },
    onFilterInput() {
      // setting through another method with debounce
    },
    setOnFilterInputFn() {
      const delay = this.apps.length > 100 ? 500 : 0;
      this.onFilterInput = debounce((e) => {
        this.filter = e.target.value;
      }, delay);
    },
  },
  watch: {
    apps: {
      handler() {
        this.setOnFilterInputFn();
      },
      immediate: true,
    },
  },
  created() {
    this.getRepo();
  },
};
</script>

